.side-bar {
  box-shadow: 0px 8px 8px 1px #41414133;
  .side-bar-content{
    position: sticky;
    top: 0;
    .logo {

      width: 100%;
      max-width: 150px;
      margin-inline: auto;
      padding-block: 20px;
  
      img {
        width: 100%;
        object-fit: contain;
      }
    }
    .side-bar-links {
      list-style: none;
      padding-inline-start: 24px;
      .link {
        a {
          display: flex;
          justify-content: space-between;
          align-items: center;
          color: black;
          padding: 6px 12px;
          .link-content {
            display: flex;
            align-items: center;
            gap: 12px;
            svg {
              height: 32px;
              width: 32px;
            }
          }
          .icon {
            transform: rotate(90deg);
            svg {
              width: 16px;
              height: 16px;
            }
          }
        }
      }
      .active {
        background: #F8FCEE;
        color: var(--main-color) !important;
        svg {
          path {
            fill: var(--main-color);
            stroke: transparent !important;
          }
        }
      }
    }
  }

}
