.side-bar {
  box-shadow: 0px 8px 8px 1px rgba(65, 65, 65, 0.2);
}
.side-bar .side-bar-content {
  position: sticky;
  top: 0;
}
.side-bar .side-bar-content .logo {
  width: 100%;
  max-width: 150px;
  margin-inline: auto;
  padding-block: 20px;
}
.side-bar .side-bar-content .logo img {
  width: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}
.side-bar .side-bar-content .side-bar-links {
  list-style: none;
  padding-inline-start: 24px;
}
.side-bar .side-bar-content .side-bar-links .link a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: black;
  padding: 6px 12px;
}
.side-bar .side-bar-content .side-bar-links .link a .link-content {
  display: flex;
  align-items: center;
  gap: 12px;
}
.side-bar .side-bar-content .side-bar-links .link a .link-content svg {
  height: 32px;
  width: 32px;
}
.side-bar .side-bar-content .side-bar-links .link a .icon {
  transform: rotate(90deg);
}
.side-bar .side-bar-content .side-bar-links .link a .icon svg {
  width: 16px;
  height: 16px;
}
.side-bar .side-bar-content .side-bar-links .active {
  background: #F8FCEE;
  color: var(--main-color) !important;
}
.side-bar .side-bar-content .side-bar-links .active svg path {
  fill: var(--main-color);
  stroke: transparent !important;
}/*# sourceMappingURL=SideBar.css.map */