.Dashboard {
  display: grid;
  grid-template-columns: 300px repeat(1, 1fr);
  height: 100vh;

  .user-dashboard {
    width: 100%;
    min-height: 100%;

    .header {
      background: var(--background-color);
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-inline: 24px 8%;
      padding-block: 40px 12px;
      border-bottom: 1px solid #a9a9a9;

      .user-info {
        display: flex;
        align-items: start;
        gap: 12px;

        .photo {
          img {
            width: 48px;
            height: 48px;
            object-fit: contain;
            border-radius: 50%;
          }
        }

        .info-content {
          .user-name {
            font-size: 14px;
          }

          .user-role {
            font-size: 8px;
          }
        }
      }

      .add-user-btn {
        padding: 8px 24px;
        background-color: var(--main-color);
        color: var(--background-color);
        border-radius: 8px;
        border: none;
      }
    }

    .user-list {
      list-style: none;

      .user-item {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        align-items: center;
        padding-inline: 4%;
        padding-block: 20px;
        border-bottom: 1px solid #a9a9a9;

        &:nth-of-type(1) {
          padding-block: 12px;
        }

        span {
          font-size: 14px;
        }

        button {
          padding: 4px 16px;
          background-color: transparent;
          border-radius: 8px;
          font-weight: 600;
          font-size: 12px;
        }

        .btn-active {
          color: var(--main-color);
          border: 2px solid var(--main-color);
        }

        .btn-none {
          color: #db4848;
          border: 2px solid #db4848;
        }
      }
    }
    .no_users {
      margin-top: 150px;
      text-align: center;
    }
  }

  .header-section {
    display: flex;
    align-items: center;
    padding-block: 40px 12px;

    .btn-action {
      display: flex;
      align-items: center;
      gap: 24px;
      margin-inline: 32px;

      button {
        display: flex;
        align-items: center;
        padding: 8px 8px;
        background-color: transparent;
        border-radius: 4px;
        font-weight: 600;
        font-size: 12px;
        border: none;
        cursor: pointer;

        &:nth-of-type(1) {
          background-color: #eff8d8;
          color: var(--main-color);
        }

        &:nth-of-type(2) {
          background-color: var(--main-color);
          color: var(--background-color);
        }
      }
    }
  }

  .input-group {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-top: 24px;
    max-width: 400px;

    input {
      padding: 6px 12px;
      width: 100%;
      border-radius: 4px;
      border: 1px solid var(--background-color);
      background-color: #fafafa;
      box-shadow: 0px 1px 8px 0px #2f2f2f29;
      outline: none;
      font-size: 16px;
      text-indent: 24px;
      padding-inline-end: 36px;

      &:focus {
        border: 1px solid var(--main-color);
        background-color: var(--background-color);
      }

      &:active {
        border: 1px solid var(--main-color);
        background-color: var(--background-color);
      }
    }

    .eye-btn {
      position: absolute;
      left: 8px;
      transform: translateY(-50%);
      top: 50%;
      height: 100%;

      svg {
        height: 100%;
      }
    }
  }

  .ql-editor {
    text-align: right;
    overflow-y: auto;
    height: 200px !important;
    max-width: 100% !important;
  }

  .add-user-form {
    width: 90%;
    max-width: 501px;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    padding: 48px;
    background-color: var(--background-color);
    box-shadow: 0px 0px 12px 0px #e7e7e7;
    border-radius: 24px;

    h4 {
      text-align: center;
      margin-bottom: 32px;
    }

    button {
      cursor: pointer;
      margin-top: 32px;
      width: 100%;
      background-color: var(--main-color);
      color: var(--background-color);
      padding: 8px 12px;
      border-radius: 6px;
      text-align: center;
      border: none;

      &:last-child {
        background-color: #494949;
        margin-top: 12px;
      }
    }

    .input-group {
      input {
        font-size: var(--title-font-descrption-two);
      }

      label {
        font-size: var(--title-font-descrption-three);
        color: #616161;
      }
    }

    select {
      padding: 6px 12px;
      width: 100%;
      border-radius: 4px;
      border: 1px solid var(--background-color);
      background-color: #fafafa;
      box-shadow: 0px 1px 8px 0px #2f2f2f29;
      outline: none;
      font-weight: 500;
      font-size: var(--title-font-descrption-two);
      text-indent: 24px;
      padding-inline-end: 36px;
      color: #696969;

      &:focus {
        border: 1px solid var(--main-color);
        background-color: var(--background-color);
      }

      &:active {
        border: 1px solid var(--main-color);
        background-color: var(--background-color);
      }
    }
  }

  .home-control {
    padding-block: 24px;
  }
  .upload-photo_btn {
    width: 100%;
    position: absolute;
    cursor: pointer;
    margin-top: 32px;
    width: 100%;
    background-color: var(--main-color);
    color: var(--background-color);
    padding: 8px 12px;
    border-radius: 6px;
    text-align: center;
    border: none;
    bottom: 0;
  }
  .feature-customization {
    padding: 24px 4%;
    border-bottom: 2px solid #a9a9a9;

    .home-control-content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-block: 32px;

      .list-feauter {
        width: 60%;
        list-style: none;

        .title {
          font-weight: 600;
          font-size: 12px;
        }
      }

      .video {
        margin-inline: 10%;
        position: relative;
        width: 30%;
        max-width: 300px;
        aspect-ratio: 6/12;
        background-color: #f8f8f8;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 12px;

        video {
          width: 100%;
          height: 100%;
          object-fit: contain;
          border-radius: 12px;
        }
      }
    }
  }

  .service-description {
    padding: 24px 4%;
    border-bottom: 2px solid #a9a9a9;

    .section-2-content {
      width: 100%;
      display: grid;
      grid-template-columns: 50% 50%;
      gap: 24px;

      h3 {
        font-size: 16px;
        font-weight: 500;
        padding: 12px;
      }
    }

    .ql-container {
      max-width: 40vw;
    }

    .ql-toolbar {
      max-width: 40vw;
    }
  }

  .content-editing {
    padding: 24px 4%;
    border-bottom: 2px solid #a9a9a9;

    .section-3-content {
      h3 {
        font-size: 16px;
        font-weight: 500;
        padding: 12px;
      }
    }

    .ql-editor {
      height: 300px !important;
    }

    .ql-container {
      max-width: 50vw;
    }

    .ql-toolbar {
      max-width: 50vw;
    }
  }

  .experience-enhancement {
    padding: 24px 4%;
    border-bottom: 2px solid #a9a9a9;

    .list-feauter {
      list-style: none;

      .input-group {
        max-width: 60%;
      }
    }

    .home-control-content {
      display: flex;
      align-items: start;
      justify-content: space-between;
      padding-block: 48px;

      .list-feauter {
        width: 60%;
        list-style: none;

        .title {
          font-weight: 600;
          font-size: 16px;
        }
      }

      .photo {
        position: relative;
        width: 40%;
        aspect-ratio: 4/4;
        background-color: #f8f8f8;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 12px;

        img {
          width: 80%;
          object-fit: contain;
          border-radius: 12px;
        }
      }
    }
  }

  .visual-and-feature {
