.Dashboard {
  display: grid;
  grid-template-columns: 300px repeat(1, 1fr);
  height: 100vh;
}
.Dashboard .user-dashboard {
  width: 100%;
  min-height: 100%;
}
.Dashboard .user-dashboard .header {
  background: var(--background-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-inline: 24px 8%;
  padding-block: 40px 12px;
  border-bottom: 1px solid #a9a9a9;
}
.Dashboard .user-dashboard .header .user-info {
  display: flex;
  align-items: start;
  gap: 12px;
}
.Dashboard .user-dashboard .header .user-info .photo img {
  width: 48px;
  height: 48px;
  -o-object-fit: contain;
     object-fit: contain;
  border-radius: 50%;
}
.Dashboard .user-dashboard .header .user-info .info-content .user-name {
  font-size: 14px;
}
.Dashboard .user-dashboard .header .user-info .info-content .user-role {
  font-size: 8px;
}
.Dashboard .user-dashboard .header .add-user-btn {
  padding: 8px 24px;
