  align-items: center;
  padding-inline: 4%;
  padding-block: 20px;
  border-bottom: 1px solid #a9a9a9;
}
.Dashboard .user-dashboard .user-list .user-item:nth-of-type(1) {
  padding-block: 12px;
}
.Dashboard .user-dashboard .user-list .user-item span {
  font-size: 14px;
}
.Dashboard .user-dashboard .user-list .user-item button {
