  background-color: transparent;
  border-radius: 8px;
  font-weight: 600;
  font-size: 12px;
}
.Dashboard .user-dashboard .user-list .user-item .btn-active {
  color: var(--main-color);
  border: 2px solid var(--main-color);
}
.Dashboard .user-dashboard .user-list .user-item .btn-none {
  color: #db4848;
  border: 2px solid #db4848;
}
.Dashboard .user-dashboard .no_users {
  margin-top: 150px;
