}
.Dashboard .header-section .btn-action button:nth-of-type(2) {
  background-color: var(--main-color);
  color: var(--background-color);
}
.Dashboard .input-group {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 24px;
  max-width: 400px;
}
.Dashboard .input-group input {
  padding: 6px 12px;
